@import url(https://fonts.googleapis.com/css?family=Montserrat:400,700);
body {
    width: 100%;
    height: 100%;

    margin: 0;
    padding: 0;
    display: table;
    position: absolute;

    font-family: 'Montserrat', serif;

    background-color: #1D1D1D;
    background: #1D1D1D url(/static/media/background.4d2aac39.png) no-repeat fixed center bottom;
    background-size: cover;

    color: white;
}

#root {
    width: 100%;
    height: 100%;

    margin: 0;
    padding: 0;
    display: table;
    position: absolute;
}

/* Layout */
#page-wrapper {
    width: 100%;
    height: 100%;
    margin: 0;
    padding: 0;

    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    text-align: center;
}

#options-wrapper {
    width: 100%;
}

.stackable {
    margin-bottom: 15px;
    display: inline-block;
}

/* Text Formatting */
.name-title {
    font-size: 15vmin;
    /*font-size: 150px;*/
    text-align: center;

    margin: 0 0 10px;

    color: #FFFDFD;
}

.role-title {
    /*font-size: 45px;*/
    font-size: 4.5vmin;
    text-align: center;

    margin: 0 0 10px;

    color: #2ac9d4;
}

.backed-text {
    margin: 10px;
    padding: 0 15px;
    background-color: rgba(29, 29, 29, 0.5);
}

.option-text {
    color: white;
    /*font-size: 40px;*/
    font-size: 4.0vmin;
    text-decoration: none;
}

.option-text:hover {
    text-decoration: overline;
}

/* Small screen options */
@media only screen and (max-width: 800px) {
    .name-title {
        font-size: 60px;
    }

    .role-title {
        font-size: 30px;
    }

    .option-text {
        font-size: 30px;
    }
}
.projects-home-link {
    color: white;
    text-decoration: none;
}

.projects-info {
    padding: 5px;
    background-color: rgba(25, 25, 25, 0.5);
    margin-bottom: 5px;
}

.projects-notable-entry {
    margin-bottom: 10px;
    line-height: 30px;

}
.overlay-page {
    width: 100%;
    height: 100%;
    position: absolute;

    display: flex;
    flex-direction: column;
    align-items: center;

    background-color: rgba(0, 0, 0, 0.7);
}

.overlay-dialog {
    margin-top: 50px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 5px;

    width: 50%;
    max-width: 700px;

    border-radius: 5px;
    background-color: #1d1d1d;
}

.overlay-close {
    min-width: 100%;
    margin-right: 10px;
    text-align: right;
    font-weight: bold;
}

/* Small screen options */
@media only screen and (max-width: 800px) {
    .overlay-dialog {
        width: 90%;
    }

}
.projects-notable-mentions, .project-card {
    max-width: 700px;
    width: 90%;

    padding: 10px 20px;
    margin: 10px 0;

    border-radius: 5px;
    background-color: rgba(25, 25, 25, 0.3);
    text-align: left;
}

.project-title, .project-subtitle, .project-company, .project-date, .project-description {
    margin: 10px 0;
    align-self: start;
}

.project-title {
    font-size: 40px;
}

.project-subtitle {
    font-size: 25px;
}

.project-image-wrapper {
    width: 100%;
    height: 150px;
    background-color: antiquewhite;
    overflow: hidden;

    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.project-image {
    width: 100%;
}

.project-company {
    width: 60%;
    display: inline-block;
}

.project-date {
    width: 40%;
    display: inline-block;
    text-align: right;
}

.project-stack-badge {
    display: inline-block;
    background-color: antiquewhite;

    padding: 3px 5px;
    margin-bottom: 10px;
    margin-right: 10px;

    border-radius: 3px;
    color: #1d1d1d;
}


.project-description {
    line-height: 30px;
}

.project-description-wrapper {
    /*max-height: 300px;*/
    /*overflow-y: scroll;*/
}

/* Small screen options */
@media only screen and (max-width: 800px) {
    .name-title {
        font-size: 60px;
    }

    .role-title {
        font-size: 30px;
    }

    .option-text {
        font-size: 30px;
    }
}
.error-status {
    font-size: 20vmin;
    margin: 10px;
}

.error-face {
    font-size: 15vmin;
    margin: 10px;

    -webkit-animation: spinning 4s linear infinite;

            animation: spinning 4s linear infinite;
}

@-webkit-keyframes spinning {
    to {
        transform: rotate(360deg);
    }
}

@keyframes spinning {
    to {
        transform: rotate(360deg);
    }
}

.error-message {
    margin: 10px;
}

.error-message a {
    color: #2ac9d4;
    -webkit-text-decoration-color: #1D1D1D;
            text-decoration-color: #1D1D1D;
    animation: flashing-underline 1s ease-in-out infinite alternate-reverse;
}

@-webkit-keyframes flashing-underline {
    to {
        -webkit-text-decoration-color: #2ac9d4;
                text-decoration-color: #2ac9d4;
    }
}

@keyframes flashing-underline {
    to {
        -webkit-text-decoration-color: #2ac9d4;
                text-decoration-color: #2ac9d4;
    }
}


